
/* global self */
import { log } from './log';
import { cacheFilesBefore, cacheFilesAfter } from './cacheFiles';

export function onInstall(e) {


  e.waitUntil(
    self.caches.open(self.cacheName)
    .then(cache => {
      log('Installed', cache);
      const isBaseApp = self.location.pathname.indexOf('base-app/') > -1;
      const cacheFilesBeforeNoDups = !isBaseApp
        ? cacheFilesBefore
        : cacheFilesBefore.filter(item => item.indexOf('base-app/') < 0);
      cache.addAll(cacheFilesAfter);
      return cache.addAll(cacheFilesBeforeNoDups);
    })
    .then(() => {
      return self.skipWaiting();
    })
    .catch(err => {
      log('Install: Error', err);
    })
  );

}
