

const urls = [ 'https://museeker.io', 'http://localhost' ];

export function getRequestType(url) {

  const isSameOrigin = url.startsWith(urls[0]) || url.startsWith(urls[1]);
  if (!isSameOrigin) return 'external';

  const isApi = url.indexOf('/api/') >= 0;
  if (isApi) return 'api';

  const isBase = url.indexOf('/base-') >= 0;
  if (isBase) return 'base';

  const isImage = url.indexOf('/icons/') >= 0 || url.indexOf('/splash/') >= 0;
  if (isImage) return 'image';

  return 'page';

}


