
/* global self */
import { log } from './log';
import { getRequestType } from './getRequestType';

let cache;

export function onFetch(e) {

  const url = e.request.url;
  const requestType = getRequestType(url);
  const skipSW = [ 'page', 'api' ].includes(requestType);
  log('RequestType', requestType);

  if (skipSW) return self.fetch(e.request);

  log('Fetching', url);
  e.respondWith(
    self.caches.open(self.cacheName)
    .then(cache_ => {
      cache = cache_;
      return cache.match(e.request);
    })
    .then(cachedResponse => {
      if (cachedResponse) {
        log('Serving from cache', cachedResponse.url);
        return cachedResponse;
      }
      return self.fetch(e.request);
    })
    .then(networkResponse => {
      e.waitUntil(
        cache.put(e.request, networkResponse.clone())
      );
      return networkResponse;
    })
  );

}


